.custom-checkbox { //       
    display: flex;
    align-items: center;
    font-size: 13px;
    line-height: 18px;
    color: #E1E5E5;
    margin: 8px 0;
}

.custom-checkbox .checkbox {
    margin-right: 10px;
}

.custom-checkbox input {
    -webkit-appearance: none; //   
    position: absolute; //     ,
                        //     
}

.checkbox { //    
    position: relative;
    width: 20px;
    height: 20px;
    
    background: #81C0D0;
    border-radius: 4px;
    
}
.get__block-wrapper{
    display: flex;
}
.checkmark { //       
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: auto;
    display: none; //  
}

.custom-checkbox input:checked ~ .checkbox .checkmark {
    display: block; //  ,   
}

.custom-checkbox input:checked ~ .checkbox {
    background: linear-gradient(274.98deg, #CF431E -83.63%, #E6B12B 100%);
    border-radius: 4px
    
    //       ,   
    //     -      
}
