.header{
  background: rgba(255, 255, 255, 0.04);
  overflow: visible;
  
  
  z-index: 1;
}

.header__first-line{
  padding: 15px 0;
  
  display: flex;
  justify-content: space-between;
}
.header__first-line-name{
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #8698A9;
}
.header__first-line-info{
  display: flex;
}
.header__first-line-info-item{
  margin-left: 24px;
  font-weight: bold;
  font-size: 14px;
  line-height: 22px;
  color: #FBFBFB;
  a{
    &:hover{
      text-decoration: underline;
    }
  }
}
.header__menu-item{
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #FBFBFB;
  cursor: pointer;
  margin-right: 40px;
  transition: all .1s linear;
 

}
.a{
  
  a{
    transition: all .1s linear;
    &:hover{
      color:#E6B12B;
     
    }
  }
 
}
.header__line{
  width: 100%;
  background: #2D4150;
  transform: matrix(1, 0, 0, -1, 0, 0);
  height: 2px;
  z-index: -1;
}

.dropbtn {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #FBFBFB;
  background: none;
  border:none;
  cursor: pointer;
  position: relative;
  img{
    transition: all .3s linear;
  }
  
 
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
  z-index: 1000;
 
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  opacity: 0;
  position: absolute;
  transition: opacity 1s linear;
  background: #1F313F;
  box-shadow: 0px 0px 20px rgba(19, 29, 36, 0.2);
  border-radius: 12px;
  width: 183px;
  z-index: 1000;
  padding: 8px 0 16px 0;

}

/* Links inside the dropdown */
.dropdown-content a {
  opacity: 1;
  transition: all .2s linear;
  padding: 8px 20px;
  text-decoration: none;
  display: block;
}
@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}
/* Change color of dropdown links on hover */
.dropdown-content a:hover { 
  color:#E6B12B;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  animation: fadeIn ease .5s;
  -webkit-animation: fadeIn ease .5s;
  -moz-animation: fadeIn ease .5s;
  -o-animation: fadeIn ease .5s;
  -ms-animation: fadeIn ease .5s;
  opacity: 1;
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  color:#E6B12B;
  img{
    transform: rotate(-180deg);
  }
}
.header__menu{
  display: flex;
  align-items: center;
}
.header__second-line{
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 0;
  
}
.header__second-line-menu{
  display: flex;

}
.log-in__btn{
  background: none;
  z-index: 99;
  border:none;
  color: #000;
  cursor: pointer;
  img{
    margin-right: 10px;
  }
 
}
.action__btn{
  border: solid 2px transparent;
  border-radius: 16px;
  background: none;
  z-index: 99;
  border:none;
  padding: 8px 20px;
  margin-left: 40px;
  background: #162631;
  position: relative;
 
  cursor: pointer;
  &:hover{
    a{
      background: linear-gradient(
274.98deg, #CF431E -83.63%, #E6B12B 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    }
  }
  
 
}
.header__second-line-buttons{
  display: flex;
  align-items: center;
}
.header__second-line-buttons-item{
  position: relative;
  cursor: pointer;
  button{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #FBFBFB;
   
   
  }
}
.log-in__btn{
   &:hover{
      background: linear-gradient(
        274.98deg, #CF431E -83.63%, #E6B12B 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
    }
}
.gradient__border{
  z-index: -1;
  background: #fff;
  position: absolute;
  width: 189px;
  height: 44px;
  border-radius: 16px;
  top: -2px;
  background: linear-gradient(274.98deg, #CF431E -83.63%, #E6B12B 100%);
  right: -2px;
}
.burger__wrapper{
  position: relative;
  z-index: 1000;
}
.burger__menu{
  position: absolute;
  width: 100%;
  z-index: 999;
  display: block;
  transition: all .4s linear;
  transform: translateX(-100%);
  
  height: 100%;
  margin-top: 60px;
  background: linear-gradient(280.34deg, #0C1D29 47.92%, #112432 66.98%, #21374D 97.75%);
}
.burger__menu-active{
  transform: translateX(0);
  display: none;
  
}
.header__menu--mobile{
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  padding-left: 0;
  margin-bottom: 100px;
  li{
    margin-bottom: 15px;
  }
  .header__menu-item{
    margin-right: 0;
  }
}