html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
a{
  color: inherit;
}
li{
  list-style: none;
}
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(280.34deg, #0C1D29 47.92%, #112432 66.98%, #21374D 97.75%);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container{
  height: 100%;
}
.site-containerr{
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}
.lock{
  overflow: hidden !important;
}
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1235px;
  
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
